import { apiUrl } from "../helper";
var socket = new WebSocket(apiUrl);
export default (function () {
  return {
    username: 'alireza',
    connectionStatus: '',
    text: '',
    messages: [],
    init: function init() {
      var _this = this;
      socket.onopen = function () {
        _this.connectionStatus = 'connection established!';
      };
      socket.onerror = function (error) {
        _this.connectionStatus = 'connection is broken!';
      };
      socket.onmessage = function (event) {
        //@ts-ignore
        _this.messages.push(event.data);
      };
    },
    sendMessage: function sendMessage() {
      //@ts-ignore
      socket.send("".concat(this.username, ": ").concat(this.text));
      //@ts-ignore
      this.messages.push("".concat(this.username, ": ").concat(this.text));
      this.text = '';
    }
  };
});